const uni = require('uni').default;










export default {
	data:{
		motto: '花卉识别',
		images: {},
		info: "",
		icrName: "",
		score: "",
		userInfo: {},
		backUserInfo: {},//后台得到的微信用户信息
		hasUserInfo: false,
		openId: "",
		nickName: "",
		baikeUrl: "",
		imageUrl: "",
		description: "",
		remark: ""
	},
	methods: {
		uploads() {
			var self = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: function (res) {
					console.log(JSON.stringify(res.tempFilePaths));
					self.imageUrl = res.tempFilePaths;
				}
			});
		}
	}
}



